export default function validateCheckout(values) {
  let errors = {};
  const regexPhone =
    /^(0?)(3[2-9]|5[6|8|9]|7[0|6-9]|8[0-6|8|9]|9[0-4|6-9])[0-9]{7}$/;

  if (!values.name) {
    errors.name = "Vui lng nhp h v tn";
  }

  if (!values.phoneNumber) {
    errors.phoneNumber = "Vui lng nhp s in thoi";
  } else if (!regexPhone.test(values.phoneNumber)) {
    errors.phoneNumber = "Vui lng nhp s in thoi hp l";
  }

  if (!values.address) {
    errors.address = "Vui lng nhp a ch";
  }

  return errors;
}
